<template>
    <div class="adduser">
        <el-card shadow="never" class="homeTop">
            <el-row>
                <el-col :span="10">
                    <div class="addTitle">
                        <i class="el-icon-s-custom"></i>
                        <span></span>
                    </div>
                </el-col>
            </el-row>
        </el-card>

        <el-card shadow="hover" class="addmain">
            <el-row>
                <el-col :span="18" :offset="2">
                    <el-form :model="adduserForm" :rules="adduserRules" ref="adduserForm" label-width="100px" status-icon>
                        <el-form-item label="" prop="nickname">
                            <el-input v-model.trim="adduserForm.nickname"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="contact">
                            <el-input v-model.trim="adduserForm.contact" ></el-input>
                        </el-form-item>
                        <el-form-item label="QQ" prop="qq">
                            <el-input v-model.trim="adduserForm.qq"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="weixin">
                            <el-input v-model.trim="adduserForm.weixin"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="zhuanye">
                            <el-input placeholder="" v-model="adduserForm.zhuanye" >
                                <el-select v-model="adduserForm.infoclass" slot="prepend" placeholder="" class="mySelect">
                                    <el-option v-for="item in infoClassList" :key="item.id" :value="item.classname" :label="item.classname"></el-option>
                                </el-select>
                            </el-input>
                        </el-form-item>

                        <el-form-item label="" prop="laiyuan">
                            <el-input v-model="adduserForm.laiyuan"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="dizhi">
                            <el-input v-model="adduserForm.dizhi"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="kefu">
                            <el-select v-model="adduserForm.kefu" placeholder="" multiple clearable>
                                <el-option v-for="item in kefuList" :key="item.id" :value="item.classname" :label="item.classname"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-row :gutter="20">
                                <el-col :span="6">
                                    <el-button type="primary" style="width:100%" @click.stop="onSubmit"></el-button>
                                </el-col>
                                <el-col :span="2">
                                    <el-button style="width:100%" @click.stop="onReset"></el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                        {{adduserForm.kefu}}
                    </el-form>
                </el-col>
            </el-row>
            
        </el-card>
    </div>
</template>
<script>

import { getKefuInfo, addUserRow } from '@/network/networks'
import Vue from 'vue'
import { Card, Row, Col, Form, FormItem, Input,Select,Option, Button, Message, MessageBox} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';
import { dataRules } from '@/assets/js/validate'
Vue.prototype.$message = Message
Vue.prototype.$confirm = MessageBox.confirm;
export default {
    data(){
        return{
            kefuList:[],
            infoClassList:[],
            adduserForm:{
                nickname:"",
                contact:'',
                qq:"",
                weixin:"",
                laiyuan:"",
                kefu:[],
                zhuanye:"",
                infoClass:""
            },
            adduserRules:{
                nickname:[
                    {required: true, message: '', trigger: 'blur'},
                    { min: 1, max: 6, message: ' 3  5 ', trigger: 'blur' }
                ],
                contact:[
                    { validator:dataRules, trigger:'blur'}
                ],
                qq:[
                    { validator:dataRules, trigger:'blur'}
                ],
                weixin:[
                    { validator:dataRules, trigger:'blur'}
                ],
                laiyuan:[
                    {required: true, message: '', trigger: 'blur'},
                ],
                zhuanye:[
                    {required: true, message: '', trigger: 'blur'},
                ],
                kefu:[
                    { required: true, message: '', trigger: 'change' }
                ]
            }
        }
    },
    mounted(){
        this.getKFInfo()
    },
    methods:{
        getKFInfo(){
            getKefuInfo().then(res => {
                this.kefuList = res.data.kefu
                this.infoClassList = res.data.infoclass
                console.log(res)
            })
        },

        //
        onSubmit(){
            this.$refs["adduserForm"].validate((valid) => {
                if(valid){
                    addUserRow(this.adduserForm).then(res => {
                        console.log(res)
                    })
                    this.$message.success("")
                    this.$router.push("/all")
                }else{
                    return this.$message.error("")
                }
            })
            
        },

        //
        onReset(){
            this.$refs["adduserForm"].resetFields()
        }
    },
    components:{
        'el-card':Card,
        'el-row':Row,
        'el-col':Col,
        'el-form':Form,
        'el-form-item':FormItem,
        'el-input':Input,
        'el-select':Select,
        'el-option':Option,
        'el-button':Button
    }
}
</script>
<style lang="less" scoped>
.addTitle{
    font-size: 28px;
    color: #409eff;
    span{
        padding-left:10px;
    }
}
.addmain{
    margin-top:20px;
    padding:30px 0;
}
.mySelect{
    width: 120px;
}
</style>